#page {
  margin: 0;
  overflow: hidden;
  height:100%;
}
#left {
  position: absolute;
  top: 80px;
  bottom: 0;
  overflow-y: scroll; 
}
#right {
  position: absolute;
  top: 80px;
  bottom: 0;
  right: 0;
  padding-left: 30px;
  overflow-y: scroll;
  color: #3c22cc;
  font-size: 90%;
}
#right a{  
  color: #545455;
}